.horizontal-image-slider div:first-child {
  display: grid;
  grid-template-columns: 10% 80% 10%;
  align-items: center;
  justify-items: center;
}

.horizontal-image-slider img {
  object-fit: cover;
  width: 100%;
  height: 40vh;
}

.horizontal-image-slider div span {
  border: 1px solid #000;
  padding: 1rem;
}

.horizontal-image-slider div span svg {
  height: 50%;
}

.horizontal-image-slider div span:nth-of-type(1) {
  transform: rotate(90deg);
}

.horizontal-image-slider div span:nth-of-type(2) {
  transform: rotate(-90deg);
}

.horizontal-image-slider .header-article-node {
  padding: 1rem;
  text-align: left;
  width: 80%;
  margin: 1rem auto;
}
